.header-action {
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  background: #2c3034;
  border: 1px solid #fd84e3;
  border-radius: 8px;
  position: relative;
  box-shadow: 1.5px 1.5px #fd84e3;
}

.header-action:before {
  content: "";
  transform-origin: center;
  width: 20px;
  height: 2px;
  background: #f3ebea;
  border-radius: 2px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 11px;
  left: 5px;
}

.header-action:after {
  content: "";
  transform-origin: center;
  width: 20px;
  height: 2px;
  background: #f3ebea;
  border-radius: 2px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 17px;
  left: 5px;
}

.header-action.open:before {
  top: 14.5px;
  left: 5px;
  transform: rotate(45deg);
}

.header-action.open:after {
  top: 14.5px;
  left: 5px;
  transform: rotate(-45deg);
}

